import { useState } from "react";

function AcademicVisaForm() {

    const [inputs, setInputs] = useState({});
    const [score, setScore] = useState(0);

    function handleCheckChanged(event) {
        const name = event.target.name;
        const value = event.target.checked;
        setInputs(values => ({...values, [name]: value}));
        
        setScore(calculateVisaPoints(inputs));
    };

    function calculateVisaPoints(inputs) {
        let currentScore = 0;
        if ( inputs.hasPHD ){
            currentScore += 30;
        } else if ( inputs.hasMA ) {
            currentScore += 20;
        } else if ( inputs.hasBA ){
            currentScore += 10;
        } else if ( inputs.hasAdditionalDegrees){
            currentScore += 5;
        }
        return currentScore;
    };

    return (
        <div>
            <div>
                <form>
                    <label>Academic Background:
                        <ul>
                            <li>
                                <label>Doctors Degree: 
                                <input type="checkbox"
                                name="hasPHD"
                                onChange={handleCheckChanged}
                                />
                                </label>
                            </li>
                            <li>
                                <label>Masters Degree: </label>
                                <input type="checkbox"
                                name="hasMA"
                                onChange={handleCheckChanged}
                                />
                            </li>
                            <li>
                                <label>Bachelors Degree:
                                <input type="checkbox"
                                name="hasBA"
                                onChange={handleCheckChanged}
                                />
                                 </label>
                            </li>
                            <li>
                                <label>Additional Degrees:
                                <input type="checkbox"
                                name="hasAdditionalDegrees"
                                onChange={handleCheckChanged}
                                />
                                </label>
                            </li>
                        </ul>
                    </label>
                </form>
            </div>
            <div>
                <h2>{score}</h2>
            </div>
        </div>
        
    )
}

export default AcademicVisaForm;